.Sobre {
    margin: 50px auto; 
    max-width: 900px; 
}

.Sobre h1 {
    font-size: 28px;
    color: #333;
    margin-bottom: 20px;
}

.Sobre h2 {
    font-size: 24px;
    color: #555;
    margin-top: 40px;
    margin-bottom: 10px;
}

.Sobre h3 {
    font-size: 20px;
    color: #777;
    margin-top: 30px;
    margin-bottom: 10px;
}

.Sobre p, .Sobre ul {
    font-size: 16px;
    line-height: 1.6;
    color: #444;
    margin-bottom: 20px;
}

.Sobre ul {
    margin-left: 20px;
}

.Sobre ul ul {
    margin-top: 10px;
    margin-left: 20px;
}

.Sobre li {
    margin-bottom: 10px;
}

.Sobre li:last-child {
    margin-bottom: 0;
}

.Sobre strong {
    color: #222;
}

.Sobre p:last-child {
    margin-bottom: 0;
}
